.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.mainClass {
    max-width: 1024px;
    margin-left: auto;
    margin-right: auto;
}

.headerClass {
    width: 100%;
    background-color: #F7F7F7;
    height: 4rem;
    border: 1px solid #EBEBEB;
    border-radius: 5px;
}

.leftHeader {
    float: left;
    margin-top: 1.3rem;
    margin-left: 10px;
    font-weight: bold;
}

.imgheader {
    float: left;
    width: 20px;
    height: 20px;
    margin-top: 1.5rem;
    margin-left: 10px;
    cursor: pointer;
}

.rightHeader {
    float: right;
    margin-top: 1.1rem;
    margin-right: 10px;
    background-color: green;
    padding-top: 3px;
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 5px;
}

.rightImageHeader {
    width: 24px;
    height: 24px;
    cursor: pointer;
}

.mainContent {
    width: 100%;
    border: 1px solid #EBEBEB;
    height: 10rem;
    margin-top: 0.6rem;
    overflow-y: auto;
    background-color: #E6E6E6;
    margin-bottom: 3rem;
}

table {
    font-family: Arial, Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

td, th {
    text-align: left;
    padding: 8px;
    font-size: 12px;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: grey;
    border-radius: 10px;
}

/* (B) EDITABLE TABLE */
.editable {
    border-collapse: collapse;
}

    .editable th, .editable td {
        border: 1px solid #000;
        padding: 10px;
    }

    .editable th {
        color: #fff;
        background: #000;
    }

    .editable td.edit {
        background: #ffe7e7;
    }
