.headerDiv {
    background-color: #026A88;
    width: 100%;
    height: 6rem;
}

.headerLeft {
    float: left;
    width: 70%;
    height: 4rem;
}

.headerRight {
    float: right;
    width: 30%;
    height: 4rem;
}

ul {
    display: inline-block;
    width: 1400px;
    margin: 48px auto;
    border-bottom: 1px solid #DAE0E7;
    list-style-type: none;
    padding: 12px 12px 0;
    margin: 8px 0;
}

.tab1 {
    float: left;
    cursor: pointer;
    margin-left: 2rem;
    margin-bottom: -1px;
    overflow: hidden;
    width: 4rem;
    text-align: center;
    padding-top: 15px;
    font-weight: bold;
    height: 45px;
    color: #4CA6D4;
    background: #fff;
}

.active {
    border: 1px solid #DAE0E7;
    color: black;
    height: 45px;
    border-radius: 8px 8px 0 0;
    border-bottom: none;
}

.tabsMain {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 3rem;
    clear: both;
}

/* Style the tab content */
.tabcontent {
    display: none;
    padding: 6px 12px;
    border-top: none;
}
